/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

.lsx-business-directory-slider .slick-dots { align-items: center; display: flex; flex-flow: row wrap; height: auto; justify-content: center; list-style: none; margin: 1rem 0 0; padding: 0; }

.lsx-business-directory-slider .slick-dots > li { line-height: 1; padding: 5px; }

@media (min-width: 992px) { .lsx-business-directory-slider .slick-dots > li { padding: 0 7.5px; } }

.lsx-business-directory-slider .slick-dots > li > button { background: transparent; border-style: solid; border-width: 1px; font: 0/0 a; height: 12px; transition: all 300ms ease-in-out; width: 4rem; }

@media (min-width: 992px) { .lsx-business-directory-slider .slick-dots > li > button { height: 6px; } }

.lsx-business-directory-slider .slick-arrow { background: transparent; border-radius: 50%; border-style: solid; border-width: 2px; font: 0/0 a; height: 4rem; margin-top: calc((2rem - 17px) / 2); opacity: 1; padding: 0; position: absolute; text-shadow: none; top: 50%; transform: translateY(-50%); transition: border 300ms ease; width: 4rem; z-index: 3; }

.lsx-business-directory-slider .slick-arrow:before { display: block; font-family: 'FontAwesome'; font-size: 3rem; line-height: 1; position: absolute; text-rendering: auto; top: 47%; transform: translateY(-50%); transition: color 300ms ease; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.lsx-business-directory-slider .slick-arrow.slick-disabled { cursor: not-allowed; }

.lsx-business-directory-slider .slick-arrow.slick-prev { left: -4rem; }

.lsx-business-directory-slider .slick-arrow.slick-prev:before { content: '\f104'; left: 1.1rem; }

.lsx-business-directory-slider .slick-arrow.slick-next { right: -4rem; }

.lsx-business-directory-slider .slick-arrow.slick-next:before { content: '\f105'; left: 1.5rem; }

